* {
  box-sizing: border-box;
  font-family: "Hanken Grotesk", Arial, sans-serif;

}

body {
  margin: 0;
  font-family: "Hanken Grotesk", Arial, sans-serif;
  background-color: #262626;
  color: #D9D9D9;
  padding: 20px;
  height: 100vh;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
header {
  justify-content: center;
  text-align: center;
  align-items: center;
}

header > h1 {
  color: #F9F4DA;
  font-weight: 500;
  font-size: 1.25rem;
  
}

header > p {
  color: #8E8E8E;
  font-size: 0.875rem;
  max-width: 350px;
  margin: 0 auto;
}

section.game-status{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #F9F4DA;
  border-radius: 4px;
  margin-block: 30px;
  min-height: 60px;
  width: 100%;
  max-width: 350px;
  justify-content: center;
}

section.game-status.won{
  background-color: #10A95B;
}

section.game-status.lost{
  background-color: #BA2A2A;
}

section.game-status.farewell {
  background-color: #7A5EA7;
  border: 1px dashed #323232;
}
section.game-status > h2 {
  font-size: 1.25rem;
  margin: 5px;
}

section.game-status > p {
  font-size: 0.875rem;
  margin: 5px;
}

section.game-status > p.farewell-message {
  font-style: italic;
  font-weight: 400;
}

section.language-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  justify-content: center;
  max-width: 350px;
  margin-bottom: 36px;
  margin: 0 auto;
}

span.chip {
  border-radius: 3px;
  padding: 4.5px;
  position: relative;
}

span.chip.lost::before {
  content: "ðŸ’€";
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  font-size: 0.85rem;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

section.word {
  display: flex;
  justify-content: center;  
  gap: 2px;
  margin-top: 36px;
}

section.word > span {
  height: 40px;
    width: 40px;
    background-color: #323232;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.125rem;
    border-bottom: 1px solid #F9F4DA;
    margin-bottom: 36px;

}

section.word > span.missed-letter {
  color: #EC5D49
}

section.keyboard{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 450px;
  gap: 8px;
  margin-bottom: 36px;
}

.keyboard > button {
  width: 35px;
  height: 35px;
  background-color: #FCBA29;
  border: 1px solid #D7D7D7;
  border-radius: 3.5px;
  cursor: pointer;
}

section.keyboard > button.correct {
  background-color: #10A95B;
}

section.keyboard > button.wrong {
  background-color: #EC5D49;
}

section.keyboard > button:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

button.new-game {
  background-color: #11B5E5;
  border: 1px solid #D7D7D7;
  border-radius: 4px;
  width: 225px;
  height: 40px;
  padding: 6px 12px;
  display: block;
  margin-inline: auto;
  cursor: pointer;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}